    font-family: sans-serif;
    /* min-width: 400px; */
    width:100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);

}
.styled-table3 thead tr {
    background-color: #3b7bbb;
    color: #ffffff;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
}


.styled-table3 th,
.styled-table3 td {
    padding: 3px 8px;
}
.styled-table3 tbody tr {
    border-bottom: 1px solid #dddddd;
}

.styled-table3 tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.styled-table3 tbody tr:last-of-type {
    border-bottom: 2px solid #264D75;
}
.styled-table3 tbody tr.active-row {
    font-weight: bold;
    color: #264d75;
}
